nav {
  width: 100%; }
  nav ul {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    margin: 0;
    padding: 0; }
    nav ul li {
      display: inline-block;
      margin: 30px; }
      nav ul li img {
        width: 3em;
        height: auto; }

main {
  display: flex;
  justify-content: center; }
  main .hero {
    display: flex;
    flex-flow: column;
    margin-bottom: 40px; }
    main .hero .hero-img img {
      width: 100%; }
    main .hero .hero-copy {
      padding: 20px; }

@media (min-width: 720px) {
  main .hero {
    align-items: center;
    flex-flow: row;
    max-width: 1280px;
    margin-bottom: 60px; }
    main .hero .hero-copy {
      padding: 30px; }
      main .hero .hero-copy h1 {
        text-align: left; } }

footer {
  background-color: #23A692;
  padding: 60px;
  color: #FFFFFF;
  text-align: center; }
  footer .socials ul {
    display: flex;
    flex-flow: row;
    justify-content: center;
    align-items: center;
    padding: 0;
    margin: 0; }
    footer .socials ul li {
      margin: 0 0.3em; }
      footer .socials ul li img {
        width: auto;
        height: 1.7em; }
  footer .socials p {
    margin: 0; }

.button {
  color: #F7F6F0;
  text-align: center;
  background-color: #23A692;
  border-radius: 10px;
  border: none;
  display: block;
  padding: 15px 30px;
  overflow: hidden;
  text-decoration: none;
  -webkit-transform: perspective(1px) translateZ(0);
  transform: perspective(1px) translateZ(0);
  box-shadow: 0 0 1px rgba(0, 0, 0, 0);
  position: relative;
  -webkit-transition-property: color;
  transition-property: color;
  -webkit-transition-duration: 0.2s;
  transition-duration: 0.2s; }
  .button:before {
    content: "";
    position: absolute;
    z-index: -1;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: #25B39D;
    -webkit-transform: scaleX(0);
    transform: scaleX(0);
    -webkit-transform-origin: 0 50%;
    transform-origin: 0 50%;
    -webkit-transition-property: transform;
    transition-property: transform;
    -webkit-transition-duration: 0.2s;
    transition-duration: 0.2s;
    -webkit-transition-timing-function: ease-out;
    transition-timing-function: ease-out; }
  .button:hover:before, .button:focus:before, .button:active:before {
    -webkit-transform: scaleX(1);
    transform: scaleX(1); }

.icon-button {
  -webkit-transition: all 1s ease-out;
  -moz-transition: all 1s ease-out;
  -o-transition: all 1s ease-out;
  transition: all 1s ease-out; }
  .icon-button:hover, .icon-button:focus {
    -webkit-transform: scale(1.2);
    transform: scale(1.2);
    -webkit-transition: all 0.2s ease-out;
    -moz-transition: all 0.2s ease-out;
    -o-transition: all 0.2s ease-out;
    transition: all 0.2s ease-out; }

body {
  font: normal 18px/150% "Poppins", Helvetica, sans-serif;
  color: #282828; }

@media (min-width: 720px) {
  body {
    font-size: 22px; } }

h1, h2, h5, a {
  color: #23A692; }

h1,
h2,
h3,
h4,
h5 {
  font-family: "Poppins", Helvetica, sans-serif;
  font-weight: 700;
  text-transform: uppercase;
  text-align: center;
  line-height: 125%; }

h1 {
  font-size: 2.2em; }

h2 {
  font-size: 1.6em; }

h3 {
  font-size: 1.25em; }

h4 {
  font-size: 1.1em; }

h5 {
  font-size: 0.8em; }

a {
  text-decoration: none; }

.text-link {
  font-weight: 700;
  border-bottom: 3px solid;
  padding-bottom: 0;
  background-size: 100% 200%;
  background-image: linear-gradient(to bottom, #FFFFFF 50%, #23A692 50%);
  -webkit-transition: background-position 100ms;
  -moz-transition: background-position 100ms;
  transition: background-position 100ms; }
  .text-link:hover {
    color: white;
    background-position: 0% 100%; }

.text-link-invert {
  font-weight: 700;
  border-bottom: 3px solid;
  padding-bottom: 0;
  color: #FFFFFF;
  background-size: 100% 200%;
  background-image: linear-gradient(to bottom, #23A692 50%, #FFFFFF 50%);
  -webkit-transition: background-position 0.1s ease-in-out;
  -moz-transition: background-position 0.1s ease-in-out;
  transition: background-position 0.1s ease-in-out; }
  .text-link-invert:hover {
    color: #23A692;
    background-position: 0% 100%; }

::selection {
  background: #333;
  color: #fff;
  text-shadow: none; }

::-moz-selection {
  background: #333;
  color: #fff;
  text-shadow: none; }

::-webkit-selection {
  background: #333;
  color: #fff;
  text-shadow: none; }

@media print {
  * {
    background: transparent !important;
    color: #000 !important;
    box-shadow: none !important;
    text-shadow: none !important; }
  a,
  a:visited {
    text-decoration: underline; }
  a[href]:after {
    content: " (" attr(href) ")"; }
  abbr[title]:after {
    content: " (" attr(title) ")"; }
  .ir a:after,
  a[href^="javascript:"]:after,
  a[href^="#"]:after {
    content: ""; }
  pre,
  blockquote {
    border: 1px solid #999;
    page-break-inside: avoid; }
  thead {
    display: table-header-group; }
  tr,
  img {
    page-break-inside: avoid; }
  img {
    max-width: 100% !important; }
  @page {
    margin: 0.5cm; }
  p,
  h2,
  h3 {
    orphans: 3;
    widows: 3; }
  h2,
  h3 {
    page-break-after: avoid; } }
